<template>
  <div class="container">
    <Products
      :key="key"
      :figures="figures"
      :per-page="10"
      :active-page="activePage"
      :visible="visible"
      type="products"
      @setResults="setResults"
      @buy="buy"
    />
  </div>
</template>
<script>
import Products from "./products/Products";
export default {
  name: "Main",
  components: { Products },
  data() {
    return {
      key: 0,
      visible: this.figures,
      activePage: 1
    };
  },
  computed: {
    figures() {
      return this.$store.state.products;
    }
  },
  serverPrefetch() {
    return this.$store.dispatch("actionProducts");
  },
  mounted() {
    if (!this.figures.length) {
      this.$store.dispatch("actionProducts");
    }
  },
  methods: {
    addProduct(figure) {
      this.$store.commit("addProduct", figure);
    },
    setResults(visible) {
      this.visible = visible;
    },
    deleteProduct(figure) {
      this.$store.commit("deleteProduct", figure);
    },
    buy({ figure }) {
      this.addProduct(figure);
      this.key++; //to Update number of selected products
    }
  }
};
</script>
<style lang="less">
@import "main.less";
</style>
